import React from 'react';
import './Styles/Circles.css';
import { Switch, Route, withRouter } from 'react-router-dom';
import {CSSTransition,TransitionGroup} from 'react-transition-group';

function Circles ({previous,menu,setMenu,navigation,location}) {

    return (
        <TransitionGroup>
            <CSSTransition
                key={location.key}
                timeout={{
                    enter: 100,
                    exit: 100
                }}
                classNames='circle-change'
            >
                <Switch location={location}>
                    <Route exact path='/skycafe'>
                        <CirclesHome navigation={navigation} previous={previous} />
                    </Route>
                    <Route exact path='/menu' >
                            <CirclesMenu menu={menu} setMenu={setMenu} navigation={navigation} previous={previous} />
                    </Route>
                    <Route exact path='/gallery' >
                            <CirclesGallery navigation={navigation} previous={previous} />
                    </Route>
                    <Route exact path='/findus' >
                            <CirclesFindUs navigation={navigation} previous={previous} />
                    </Route>
                </Switch>
            </CSSTransition>
        </TransitionGroup>
    )
}

function CirclesHome ({previous,navigation}) {
    return (
        <div className={`n1st-circle ${navigation==='skycafe' && previous!=='skycafe' ? 'take-to-center' : 'circle-center'}`}>
            <div className="n2nd-circle">
                <div className="n3rd-circle">
                    <div className="n4th-circle">
                        <div className="n5th-circle">
                            <div className="n6th-circle">
                                <p>Don't wait until<br/>Friday</p>
                                <a className='n7th-circle' href="#">
                                    <p>Reserve<br/>NOW</p>
                                </a>
                                <a href="#">Compare dates</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

function CirclesMenu ({previous,menu,setMenu,navigation}) {

    function changeMenu (newstate) {
        setMenu(newstate);
    }
    return (
        <div className={`n1st-circle ${navigation==='menu' && previous==='skycafe' ? 'take-to-left' : 'circle-left'}`}>
            <div className="n2nd-circle ">
                <div className="n3rd-circle n3rd-circle-menu">
                    <div className = {`menu-container ${menu}-whitebg`}>
                        <p onClick={()=>changeMenu('starters')} className={`starters ${menu==='starters' ? 'active-color' : ""}`}>Starters</p>
                        <p onClick={()=>changeMenu('salads')} className={`salads ${menu==='salads' ? 'active-color' : ""}`}>Salads</p>
                        <p onClick={()=>changeMenu('main')} className={`main ${menu==='main' ? 'active-color' : ""}`}>Main</p>
                        <p onClick={()=>changeMenu('vegan')} className={`vegan ${menu==='vegan' ? 'active-color' : ""}`}>Vegan</p>
                        <p onClick={()=>changeMenu('desserts')} className={`desserts ${menu==='desserts' ? 'active-color' : ""}`}>Desserts</p>
                        <p onClick={()=>changeMenu('drinks')} className={`drinks ${menu==='drinks' ? 'active-color' : ""}`}>Drinks</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

function CirclesGallery ({previous,navigation}) {
    return (
        <div className={`n1st-circle ${navigation==='gallery' && previous==='skycafe' ? 'take-to-left' : 'circle-left'}`}>
            <div className="n2nd-circle ">
                <div className="n3rd-circle n3rd-circle-gallery">
                    
                </div>
            </div>
        </div>
    )
}

function CirclesFindUs ({previous,navigation}) {
    return (
        <div className={`n1st-circle ${navigation==='findus' && previous==='skycafe' ? 'take-to-left' : 'circle-left'}`}>
            <div className="n2nd-circle ">
                <div className="n3rd-circle n3rd-circle-findus">
                    
                </div>
            </div>
        </div>
    )
}

export default withRouter(Circles);