import React, { useState } from 'react';
import './App.css';
import Header from './Header';
import Centerpiece from './Centerpiece';
import {BrowserRouter as Router} from 'react-router-dom';
import Footer from './Footer';

function App() {

  const [navigation, setNavigation] = useState('unchanged');
  const [previous, setPrevious] = useState('unchanged');
  const [menu,setMenu] = useState('starters');

  return (
    <Router>
      <div className="App">
        <Header setMenu={setMenu} navigation={navigation} setNavigation={setNavigation} setPrevious={setPrevious} previous={previous} />
        <Centerpiece previous={previous} navigation={navigation} menu={menu} setMenu={setMenu} setNavigation={setNavigation}/>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
