import React from 'react';
import './Styles/Centerpiece.css';
import Circles from './Circles';
import Backdrop from './Backdrop';

function Centerpiece ({previous,navigation,menu,setMenu}) {
    return (
        <div className='centerpiece'>
            <Backdrop previous={previous} navigation={navigation} menu={menu}/>
            <Circles previous={previous} menu={menu} setMenu={setMenu} navigation={navigation} />
        </div>
    )
}

export default Centerpiece;