import React from 'react';
import './Styles/Backdrop.css';
import {Route, Switch, withRouter} from 'react-router-dom';
import {CSSTransition,TransitionGroup} from 'react-transition-group';

function Backdrop ({previous,navigation,location, menu}) {
    return (
        <TransitionGroup className='backdrop-parent'>
            <CSSTransition
                key={location.key}
                timeout={{
                    enter: 500,
                    exit: 500
                }}
                classNames='backdrop-change'
            >
                <section className='backdrop-single'>
                    <Switch location={location}>
                        <Route exact path='/skycafe'>
                            <BackdropHome />
                        </Route>
                        <Route path='/menu'>
                            <BackdropMenu navigation={navigation} menu={menu} previous={previous}/>
                        </Route>
                        <Route path='/gallery'>
                            <BackdropGallery navigation={navigation} previous={previous}/>
                        </Route>
                        <Route path='/findus'>
                            <BackdropFindUs navigation={navigation} previous={previous}/>
                        </Route>
                    </Switch>
                </section>
            </CSSTransition>
        </TransitionGroup>
    )
}

function BackdropHome () {
    return (
        <div className='backdrop'>
            <div className='left-pic'>
            </div>
            <div className='right-pic'>
            </div>
            <div className="color-shade">
                <p>Get together under<br/>one sky</p>
                <p>Sky high conversations<br/>start here</p>
            </div>
        </div>
    )
}

function BackdropMenu ({navigation, menu, previous}){
    return (
        <div className='backdrop backdrop-purple'>
            <div className={`menu-list ${navigation==='menu' && previous==='skycafe' ? 'white-container-appear' : ''}`}>
                <TheMenu menu={menu} />
            </div>
        </div>
    )
}

function TheMenu ({menu}) {

    const food_list = {
    'starters': ['Lobster Rolls','Beef Tataki','Lobster Tempura','Corvina Tiradito','Ribs & Chips','Seared Foie Gras','Wild Mushrooms'],
    'salads': ['Grilled Chicke','Club Life','Italian Morning','Grilled Tirapila','Stuffed Tomato'],
    'main': ['Beer Battered Haddock','Pan Fried Fillet','Seared Scallops','Fresh Dressed Crab','Thai Green Noodle'],
    'vegan': ['Yellow Dal Tadka','Aldo Sabzi','Fennel Pollin','Fried Mushrooms','Soya Curd'],
    'desserts': ['Maple Bread Pudding','Carrot Cake','Chocolate Cheesecake','Wilcox Icecream','Brownie Sundae'],
    'drinks': ['Coca-Cola','Fanta','Sprite','Tea','Latte','Cappucino','Hot Chocolate']
    }
    return(
        <div className="the-menu">
            <h2 className='menu-header'>{`${menu[0].toUpperCase()+menu.substring(1)}`}</h2>
            {food_list[menu].map(food => {
                return (
                    <div className='the-food'>
                        <p>{food}</p>
                        <div className='under-line'></div>
                        <p>$5</p>
                    </div>
                )
            })
            }
        </div>
    )
}

function BackdropGallery ({navigation,previous}) {
    return (
        <div className='backdrop backdrop-purple'>
            <div className={`findus-white ${navigation==='gallery' && previous==='skycafe' ? 'white-container-appear' : ''}`}>
                <div className='gallery-grid'>
                    <div className="photo"><img src={ require('./img/gallery-pics/pic1.jpg')} alt=""/></div>
                    <div className="photo"><img src={ require('./img/gallery-pics/pic2.jpg')} alt=""/></div>
                    <div className="photo"><img src={ require('./img/gallery-pics/pic3.jpg')} alt=""/></div>
                    <div className="photo"><img src={ require('./img/gallery-pics/pic4.jpg')} alt=""/></div>
                    <div className="photo"><img src={ require('./img/gallery-pics/pic5.jpg')} alt=""/></div>
                    <div className="photo"><img src={ require('./img/gallery-pics/pic6.jpg')} alt=""/></div>
                </div>
            </div>
        </div>
    )
}

function BackdropFindUs ({navigation,previous}) {
    return (
        <div className='backdrop backdrop-purple'>
            <div className={`findus-white ${navigation==='findus' && previous==='skycafe' ? 'white-container-appear' : ''}`}>
                <div className="contact">
                    <h2>Call Us</h2>
                    <a href="tel: +00000000"><i className="fas fa-phone-alt"></i>+994 55 000 00 00</a>
                    <a href="tel: +00000000"><i className="fas fa-phone-alt"></i>+994 77 000 00 00</a>
                    <h2>Email Us</h2>
                    <a href="mailto: skycafe@example.com"><i className="fas fa-envelope"></i>skycafe@example.com</a>
                </div>
                <div className="map">
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d194472.674775229!2d49.71487495796301!3d40.394769469930296!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x40307d6bd6211cf9%3A0x343f6b5e7ae56c6b!2sBaku%2C%20Azerbaijan!5e0!3m2!1sen!2s!4v1602354806452!5m2!1sen!2s" frameBorder="0" ></iframe>
                </div>
            </div>
        </div>
    )
}

export default withRouter(Backdrop);